@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
  --title-font: 'Quicksand', sans-serif;
  --light-accent-color: #00E9AA;
  --dark-accent-color: #84FFC5;
  --accent-color: var(--dark-accent-color);

  --light-bg: white;
  --dark-bg: rgb(14, 14, 15);
  --bg: var(--light-bg);
  --cursor-color: black;
}

body {
  background: white;
  background: var(--bg);
}

li {
  cursor: pointer;
}

h1 {
  font-family: 'Quicksand', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-family: var(--title-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 800;
  line-height: 3rem;
}

p {
  line-height: 1.6rem;
}
.events h1 {
  line-height: 2rem;
}

.events > h1 {
  padding: 20px;
  padding-bottom: 0;
}

.events .main-event {
  position: relative;
  min-height: 50vh;
  display: flex;
  align-items: center;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.2));
}

.events .main-event .about {
  padding: 30px;
  width: 60%;
}

.events .main-event .about h2 {
  padding-bottom: 0;
}

.events .main-event .about .date-time {
  font-size: .8rem;
  font-weight: 600;
}

.events .main-event .about .register-btn {
  padding: 10px 20px;
  margin-top: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: 600;
  background: var(--accent-color);
  cursor: pointer;
}

.events .main-event .event-img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
}

.events .main-event .event-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.all-events {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
}

.all-events .event {
  /* width: 300px; */
  height: 200px;
  overflow-y: hidden;
  position: relative;
  transition: box-shadow 100ms ease, -webkit-transform 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease, -webkit-transform 100ms ease;
  /*  &::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 50px;
            background: linear-gradient(to top, var(--light-bg), rgba(0, 0, 0, 0));
            z-index: 10;
        } */
}

.all-events .event .about {
  height: 100%;
  padding: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.856);
}

.all-events .event:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.192);
  z-index: 100;
}

.all-events .event .bg-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  overflow: hidden;
}

.all-events .event .bg-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 700px) {
  .events .main-event {
    background: rgba(255, 255, 255, 0.8);
  }
  .events .main-event .about {
    padding: 24px;
    width: 100%;
  }
  .all-events {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
  }
}
